@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Text:ital,wght@0,400;0,500;0,700;1,400;1,500;1,700&display=swap);
.bolsas-favoritas {
    padding-bottom: 15rem;
}

.bolsas-favoritas .bolsas-favoritas__title {
    margin-top: 3rem;
}

.bolsas-favoritas .bolsas-favoritas__description {
    margin-top: 1rem;
    font-size: var(--text-size);
}

.bolsas-favoritas .bolsas-favoritas__filter {
    margin-top: 3.5rem;
}

.bolsas-favoritas .bolsas-favoritas__scholarship {
    margin-top: 3.5rem;
}

.button {
    border: 1px solid var(--blue-secondary);
    padding: 1.2rem 2rem;
    border-radius: 0.5rem;
    color: var(--blue-secondary);
    font-weight: var(--font-weight-bolder);
    font-family: var(--font-family);
    font-size: var(--text-button-size);
    cursor: pointer;
}

.nav-group__item {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding-right: 2rem;
    padding-left: 2rem;
    font-weight: var(--font-weight-bold);
}

.nav-group__item--activated {
    background: var(--blue-secondary);
}

@media screen and (min-width: 1200px) {
    .nav-group__item {
        font-size: var(--text-size);
    }

    .nav-group__item:hover {
        cursor: pointer;
        transition: all 0.3s ease;
        background: var(--blue-secondary);
    }
}

.menu__toggle {
    background: none;
    outline: 0;
    border: 0;
    font-size: inherit;
    font-weight: inherit;
    color: inherit;
}

.menu__toggle > span,
.menu__toggle > svg {
    vertical-align: middle;
}

.menu__toggle > span {
    margin-right: 0.6rem;
}

@media screen and (min-width: 620px) {
    .menu__toggle {
        display: none;
    }
}

.title {
    font-size: var(--title-section-size);
    color: var(--text-color);
}

.text {
    font-weight: var(--font-weight-regular);
    color: var(--text-color);
}

.card {
    width: 100%;
    padding: 2.5rem 1rem;
    box-shadow: 0px 1px 4px 0px var(--box-shadow-color);
}

.header-link {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    color: var(--blue-secondary);
}

.header-link:hover {
    cursor: pointer;
    opacity: 0.9;
    transition-duration: 0.3s;
}

.header-link .header-link__icon {
    width: 2.5rem;
}

.header-link .header-link__text p {
    font-weight: var(--font-weight-bolder);
    font-size: 1.3rem;
}

.header-link .header-link__text p span {
    display: block;
}

@media screen and (min-width: 1200px) {
    .header-link {
        flex-direction: row;
    }

    .header-link .header-link__text {
        margin-left: 1rem;
    }

    .header-link .header-link__text p {
        font-size: var(--text-size);
    }

    .header-link .header-link__text p span + span {
        margin-top: 0.5rem;
        font-size: var(--small-text-size);
    }
}

.breadcrumb {
    color: var(--blue-secondary);
    font-weight: var(--font-weight-bolder);
    font-size: var(--small-text-size);
}

.breadcrumb .breadcrumb__icon,
.breadcrumb .breadcrumb__item {
    vertical-align: middle;
}

.breadcrumb .breadcrumb__icon {
    margin-right: 0.8rem;
}

@media screen and (min-width: 960px) {
    .breadcrumb .breadcrumb__item:not(:last-of-type)::after {
        content: "/";
        margin: 0 1rem;
        font-weight: var(--font-weight-regular);
        color: var(--text-color);
    }

    .breadcrumb .breadcrumb__item:last-of-type {
        color: var(--text-color);
        font-weight: var(--font-weight-bold);
    }

    .breadcrumb .breadcrumb__item:hover {
        cursor: pointer;
        transition: all 0.3s ease;
        opacity: 0.9;
    }
}

.semester-filter {
    width: 100%;
}

.semester-filter .semester-filter__button {
    width: 100%;
}

.semester-filter .semester-filter__button.semester-filter__button--selected {
    background: var(--blue-secondary);
    color: var(--text-white);
}

.semester-filter .semester-filter__button:first-of-type {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}

.semester-filter .semester-filter__button:nth-of-type(2) {
    border-radius: 0;
}

.semester-filter .semester-filter__button:last-of-type {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}

@media screen and (min-width: 620px) {
    .semester-filter {
        display: flex;
        justify-content: flex-end;
    }

    .semester-filter .semester-filter__button {
        width: auto;
        padding: 0.5rem 2rem;
        font-size: 1.5rem;
    }

    .semester-filter .semester-filter__button:first-of-type {
        border-radius: 0.5rem 0 0 0.5rem;
    }

    .semester-filter .semester-filter__button:last-of-type {
        border-radius: 0 0.5rem 0.5rem 0;
    }
}

.scholarship-list__button {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-right: 4rem;
    padding-left: 4rem;
    cursor: pointer;
}

.scholarship-list__button > svg {
    font-size: 6rem;
    color: var(--blue-primary);
    transition: all 0.3s ease-in-out;
}

.scholarship-list__button > h3 {
    font-size: var(--large-text-size);
    margin-top: 2rem;
}

.scholarship-list__button > p {
    font-size: var(--text-button-size);
    text-align: center;
}

@media screen and (min-width: 620px) {
    .scholarship-list__button {
        min-height: 40rem;
    }
}

@media screen and (min-width: 1200px) {
    .scholarship-list__button:hover > svg {
        transform: scale(1.2);
        transition: all 0.3s ease-in-out;
    }
}

.scholarship-list__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1.5rem;
    text-align: center;
}

/* Header */

.scholarship-header {
    border-bottom: var(--border-header-link);
    width: 100%;
    height: 100%;
    padding-bottom: 1rem;
}

.scholarship-header .scholarship-header__logo {
    max-width: 12rem;
    max-height: 6rem;
}

.scholarship-header .scholarship-header__university {
    font-size: var(--small-text-size);
    text-transform: uppercase;
    margin-top: 2rem;
}

.scholarship-header .scholarship-header__course {
    font-size: var(--text-button-size);
    font-weight: var(--font-weight-bolder);
    color: var(--blue-secondary);
    margin-top: 0.5rem;
}

.scholarship-header .scholarship-header__score {
    margin-top: 1rem;
}

.scholarship-header .scholarship-header__score > span,
.scholarship-header .scholarship-header__score > svg {
    font-size: var(--text-button-size);
    vertical-align: middle;
}

.scholarship-header .scholarship-header__score > span {
    font-weight: var(--font-weight-bolder);
    margin-right: 1rem;
}

.scholarship-header .scholarship-header__score > svg {
    color: var(--yellow-primary);
    margin-right: 0.5rem;
}

/* Details */
.scholarship-details {
    border-bottom: var(--border-header-link);
    width: 100%;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
}

.scholarship-details .scholarship-details__kind {
    font-size: var(--small-text-size);
    font-weight: var(--font-weight-bolder);
    text-transform: uppercase;
}

.scholarship-details .scholarship-details__start-date {
    font-size: var(--small-text-size);
    font-weight: var(--font-weight-regular);
    margin-top: 1rem;
}

/* Price */
.scholarship-price {
    width: 100%;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
}

.scholarship-price p:first-of-type {
    font-size: var(--small-text-size);
    font-weight: var(--font-weight-bolder);
}

.scholarship-price .scholarship-price__full {
    font-size: var(--small-text-size);
    font-weight: var(--font-weight-bold);
    margin-top: 1rem;
    text-decoration: line-through;
}

.scholarship-price .scholarship-price__with-discount {
    font-size: var(--text-size);
    font-weight: var(--font-weight-bolder);
    color: var(--green);
}

.scholarship-price .scholarship-price__with-discount > span {
    font-size: var(--small-text-size);
    font-weight: var(--font-weight-regular);
    color: var(--text-color);
}

/* Message */
.scholarship-message {
    width: 100%;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
}

.scholarship-message p:first-of-type {
    font-size: var(--small-text-size);
    font-weight: var(--font-weight-bolder);
}

.scholarship-message .scholarship-message__description {
    font-size: var(--small-text-size);
    font-weight: var(--font-weight-regular);
    margin-top: 1rem;
}

/* Actions */
.scholarship-actions {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-column-gap: 1.5rem;
    -webkit-column-gap: 1.5rem;
            column-gap: 1.5rem;
    margin-top: 1rem;
}

.scholarship-actions > .scholarship-actions__view {
    background: var(--yellow-primary);
    border-color: var(--yellow-secondary);
    color: var(--text-color);
}

.scholarship-actions > .scholarship-actions__view.scholarship-actions__view--disabled {
    background: var(--bg-disabled);
    border-color: #adb1b2;
    color: var(--text-disabled);
}

.scholarship-actions > .scholarship-actions__view.scholarship-actions__view--disabled:hover {
    cursor: not-allowed;
}

@media screen and (min-width: 1200px) {
    .scholarship-actions > .scholarship-actions__remove,
    .scholarship-actions > .scholarship-actions__view,
    .scholarship-actions > .scholarship-actions__view.scholarship-actions__view--disabled {
        padding: 0.5rem 1.5rem;
    }
}

.modal-wrapper {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: auto;
    background: var(--modal-overlay);
    visibility: hidden;
    opacity: 0;
    overflow-y: scroll;
    transition: all 0.3s ease;
}

.modal-wrapper.visible {
    visibility: visible;
    opacity: 1;
    transition: all 0.3s ease;
}

.modal-wrapper .modal {
    width: 100%;
    background: #fff;
    position: relative;
    padding: 2rem;
    margin: 7rem auto;
}

.modal-wrapper .modal .modal__close {
    position: absolute;
    top: -5rem;
    right: 1rem;
    font-size: 3rem;
    color: var(--text-white);
}

.modal-wrapper .modal .modal__close:hover {
    cursor: pointer;
}

@media screen and (min-width: 620px) {
    .modal-wrapper .modal {
        max-width: 75rem;
        padding: 4rem 3.5rem;
    }

    .modal-wrapper .modal .modal__close {
        top: -5rem;
        right: -1rem;
        font-size: 4.5rem;
    }
}

.form-select {
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem;
}

.form-select .form-select__label {
    font-size: var(--small-text-size);
    font-weight: var(--font-weight-bolder);
    text-transform: uppercase;
}

.form-select .form-select__field {
    font-size: var(--text-button-size);
    font-weight: var(--font-weight-bold);
    padding: 1rem;
    border: 1px solid var(--bg-disabled);
    border-radius: 0.5rem;
}

.form-checkbox {
    display: flex;
    flex-direction: column;
}

.form-checkbox .form-checkbox__label {
    font-size: 1.5rem;
    font-weight: var(--font-weight-regular);
}

.form-range .form-range__label {
    font-size: var(--small-text-size);
    font-weight: var(--font-weight-bolder);
    text-transform: uppercase;
}

.form-range .form-range__currency {
    font-size: var(--text-button-size);
    font-weight: var(--font-weight-regular);
    text-transform: uppercase;
}

.form-range .form-range__field {
    width: 100%;
    margin-top: 2rem;
}

.scholarship-result-item {
    display: grid;
    grid-template-columns: auto 8rem 1fr;
    grid-template-rows: auto auto;
    grid-gap: 2rem 2.5rem;
    gap: 2rem 2.5rem;
    align-items: center;
    padding: 1.5rem 0;
    border-top: var(--border-header-link);
}

.scholarship-result-item:last-of-type {
    border-bottom: var(--border-header-link);
}

.scholarship-result-item .scholarship-result-item__checkbox {
    grid-row-start: 1;
    grid-row-end: 3;
}

.scholarship-result-item .scholarship-result-item__logo {
    width: 100%;
    grid-row-start: 1;
    grid-row-end: 3;
}

.scholarship-result-item .scholarship-result-item__course > p:first-of-type {
    font-size: var(--text-button-size);
    font-weight: var(--font-weight-bolder);
    color: var(--blue-secondary);
}

.scholarship-result-item .scholarship-result-item__course > p:last-of-type {
    font-size: var(--small-text-size);
    font-weight: var(--font-weight-bold);
}

.scholarship-result-item .scholarship-result-item__price {
    grid-column-start: 3;
    grid-row-start: 2;
}

.scholarship-result-item .scholarship-result-item__price > p:first-of-type {
    font-size: var(--text-button-size);
    font-weight: var(--font-weight-bold);
}

.scholarship-result-item .scholarship-result-item__price > p:first-of-type > span {
    font-weight: var(--font-weight-bolder);
    color: var(--green);
}

.scholarship-result-item .scholarship-result-item__price > p:last-of-type {
    font-size: var(--text-button-size);
    font-weight: var(--font-weight-bolder);
    color: var(--green);
}

@media screen and (min-width: 620px) {
    .scholarship-result-item {
        grid-template-columns: auto 8rem auto 1fr;
        grid-template-rows: 8rem;
    }

    .scholarship-result-item .scholarship-result-item__checkbox {
        grid-row-end: 2;
    }

    .scholarship-result-item .scholarship-result-item__logo {
        grid-row-end: 2;
    }

    .scholarship-result-item .scholarship-result-item__price {
        grid-column-start: 7;
        grid-row-start: 1;
        text-align: right;
    }
}

.scholarship-result-filter {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.scholarship-result-filter > span:first-of-type,
.scholarship-result-filter .scholarship-result-filter__action {
    font-size: var(--text-button-size);
    font-weight: var(--font-weight-bolder);
}

.scholarship-result-filter .scholarship-result-filter__action {
    text-align: right;
}

.scholarship-result-filter .scholarship-result-filter__action > select {
    font: inherit;
    border: none;
    outline: 0;
    color: var(--blue-secondary);
}

.header {
    height: var(--header-height);
    display: flex;
}

.header .header__grid {
    display: grid;
    grid-template-columns: auto 1fr auto;
    align-items: center;
}

.header .header__logo {
    width: 100%;
    padding: 0 1rem;
    border-right: var(--border-header-link);
    border-left: var(--border-header-link);
    text-align: center;
}

.header .header__logo > svg {
    width: 12rem;
}

.header .header__left-side {
    display: flex;
    padding-right: var(--padding-side);
}

.header .header__right-side {
    display: flex;
    padding-left: var(--padding-side);
}

.header .header__left-side > .header-link:not(:first-of-type) {
    display: none;
}

@media screen and (min-width: 1200px) {
    .header .header__grid {
        grid-template-columns: 40rem 1fr 40rem;
    }

    .header .header__logo {
        border: none;
    }

    .header .header__logo > svg {
        width: 15rem;
    }

    .header .header__left-side {
        padding-right: 0;
    }

    .header .header__right-side {
        justify-content: flex-end;
    }

    .header .header__left-side > .header-link:first-of-type {
        padding-right: 2rem;
        border-right: var(--border-header-link);
    }

    .header .header__left-side > .header-link:not(:first-of-type) {
        display: flex;
        margin-left: 2rem;
    }

    .header .header__left-side > .header-link:not(:first-of-type) .header-link__icon {
        color: var(--green);
    }

    .header .header__right-side > .header-link {
        flex-direction: row-reverse;
        margin-right: 0;
    }

    .header .header__right-side > .header-link > .header-link__text {
        margin-right: 1rem;
    }
}

.menu {
    background: var(--blue-primary);
    height: 4rem;
    margin-bottom: 2.5rem;
    box-shadow: inset 0px 1px 3px 0px #808080;
}

.menu .menu__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    font-size: var(--small-text-size);
    font-weight: var(--font-weight-bolder);
    color: var(--text-white);
}

.menu .menu__account {
    font-weight: var(--font-weight-bold);
}

.menu .nav-group {
    display: none;
}

@media screen and (min-width: 620px) {
    .menu .menu__inner {
        justify-content: flex-start;
    }

    .menu .menu__account {
        margin-right: 3rem;
    }

    .menu .nav-group {
        display: grid;
        grid-template-columns: 1fr 1fr;
        height: 100%;
    }
}

@media screen and (min-width: 1200px) {
    .menu .menu__account {
        font-size: var(--large-text-size);
    }
}

.footer {
    background: var(--blue-primary);
}

.footer .footer-content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-gap: 0.2rem;
    gap: 0.2rem;
}

.footer .footer-content .header-link {
    width: 100%;
    height: 100%;
    justify-content: center;
    background: var(--blue-secondary);
    color: var(--text-white);
}

.footer .footer-content .header-link:first-of-type {
    grid-column-start: 1;
    grid-column-end: 4;
    display: flex;
    flex-direction: row;
}

.footer .footer-content .header-link:first-of-type .header-link__icon {
    margin-right: 1.5rem;
}

.footer .footer-content .header-link:first-of-type .header-link__text span:first-of-type {
    font-weight: var(--font-weight-bold);
    font-size: var(--text-size);
}

.footer .footer-content .header-link:first-of-type .header-link__text span + span {
    font-weight: var(--font-weight-regular);
    font-size: var(--text-size);
}

.footer .footer-content .header-link .header-link__text p {
    font-weight: var(--font-weight-bold);
    font-size: var(--text-size);
}

.footer .footer-content .footer-content__copyright {
    grid-column-start: 1;
    grid-column-end: 4;
    text-align: center;
    color: var(--text-white);
    font-weight: var(--font-weight-bold);
    font-size: var(--small-text-size);
    padding: 3rem 0;
}

@media screen and (min-width: 1200px) {
    .footer .footer-content {
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: 1fr 1fr;
    }

    .footer .footer-content .header-link:first-of-type {
        grid-column-start: 1;
        grid-column-end: 2;
        display: flex;
        flex-direction: row;
    }

    .footer .footer-content .header-link:first-of-type .header-link__icon {
        margin-right: 0;
    }

    .footer .footer-content .header-link .header-link__text p {
        font-weight: var(--font-weight-bold);
        font-size: var(--text-size);
    }

    .footer .footer-content .header-link .header-link__text span:first-of-type {
        font-weight: var(--font-weight-bolder);
    }

    .footer .footer-content .header-link .header-link__text span + span {
        font-weight: var(--font-weight-regular);
        font-size: var(--text-size);
    }

    .footer .footer-content .footer-content__copyright {
        grid-column-start: 1;
        grid-column-end: 5;
        color: var(--text-white);
        font-weight: var(--font-weight-bold);
        font-size: var(--small-text-size);
        padding: 5rem 0;
    }
}


.scholarship-list {
    display: grid;
    justify-content: space-between;
}

.scholarship-list .scholarship-list__button,
.scholarship-list .scholarship-list__item {
    margin-bottom: 2rem;
}

@media screen and (min-width: 620px) {
    .scholarship-list {
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 2rem;
        -webkit-column-gap: 2rem;
                column-gap: 2rem;
    }

    .scholarship-list .scholarship-list__item {
        min-height: 40rem;
    }
}

@media screen and (min-width: 960px) {
    .scholarship-list {
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media screen and (min-width: 1200px) {
    .scholarship-list {
        grid-template-columns: repeat(4, 27.5rem);
    }
}

.modal-scholarship .modal-scholarship__title {
    font-size: var(--extra-large-text-size);
}

.modal-scholarship .modal-scholarship__description {
    font-size: var(--text-button-size);
    font-weight: var(--font-weight-bold);
}

.modal-scholarship .modal-scholarship__form {
    margin-top: 3rem;
    display: grid;
}

.modal-scholarship .modal-scholarship__filter {
    margin-top: 3rem;
}

.modal-scholarship .modal-scholarship__result {
    margin-top: 2rem;
}

.modal-scholarship .modal-scholarship__actions {
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    margin-top: 2.5rem;
}

.modal-scholarship .modal-scholarship__actions--primary {
    background: var(--yellow-primary);
    border-color: var(--yellow-secondary);
    color: var(--text-color);
}

.modal-scholarship .modal-scholarship__actions--disabled {
    background: var(--bg-disabled);
    border-color: #adb1b2;
    color: var(--text-disabled);
}

@media screen and (min-width: 620px) {
    .modal-scholarship .modal-scholarship__form {
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 1.5rem;
        -webkit-column-gap: 1.5rem;
                column-gap: 1.5rem;
        margin-top: 4rem;
    }

    .modal-scholarship .modal-scholarship__actions {
        justify-content: flex-end;
    }

    .modal-scholarship .modal-scholarship__actions button:first-of-type {
        margin-right: 1.5rem;
    }
}

.checkbox-group {
    margin-bottom: 2rem;
}

.checkbox-group .checkbox-group__label {
    font-size: var(--small-text-size);
    font-weight: var(--font-weight-bolder);
    text-transform: uppercase;
}

.checkbox-group .checkbox-group__fields {
    display: flex;
    margin-top: 2rem;
}

.checkbox-group .checkbox-group__fields .form-checkbox {
    margin-right: 2rem;
}

.checkbox-group .checkbox-group__fields .form-checkbox .form-checkbox__field {
    margin-right: 0.5rem;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
}

html {
    font-size: 62.5%;
}

body {
    background: var(--bg-default);
    font-family: var(--font-family);
    min-height: 100vh;
    color: var(--text-color);
}

ul,
ol {
    list-style: none;
}

a {
    color: inherit;
    text-decoration: inherit;
}

button {
    outline: 0;
    background: none;
    border: none;
}



:root {
    --blue-primary: #18acc4;
    --blue-secondary: #007a8d;
    --yellow-primary: #fdcb13;
    --yellow-secondary: #de9e1f;
    --green: #0fa866;
    --text-color: #1f2d30;
    --bg-default: #fbfbfb;
    --bg-disabled: #cacdce;
    --text-disabled: #828a94;
    --text-white: #fbfbfb;
    --modal-overlay: rgba(31, 45, 48, 0.88); /* #1f2d30 */
    --box-shadow-color: #a9a9a9;

    --border-header-link: 3px solid #eeefef;
}

:root {
    --font-family: "Red Hat Text", sans-serif;
    --font-weight-bold: 500;
    --font-weight-bolder: 600;
    --font-weight-regular: 400;
}

:root {
    /* Fonts */
    --text-size: 1.6rem;
    --small-text-size: 1.2rem;
    --large-text-size: 1.8rem;
    --extra-large-text-size: 2rem;
    --title-section-size: 3rem;

    --text-button-size: 1.4rem;

    /* Paddings */
    --padding-side: 1rem;
    --padding-box: 1rem 1.5rem;

    /* Header */
    --header-height: 7.5rem;
}



.container {
    max-width: 1200px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-left: var(--padding-side);
    padding-right: var(--padding-side);
}

.d-flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
}





